import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import OrientationDisplay from '../components/orientation/OrientationDisplay';
import OrientationTest from '../components/orientation/OrientationTest';

class Tremor extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      orientationEvent:null,
      canPlay:false,
    }
  }
  
  componentDidMount(){window.addEventListener("deviceorientation", this.handleOrientation.bind(this));}
  componentWillUnmount(){window.removeEventListener('deviceorientation', this.handleOrientation.bind(this));}

  handleOrientation = (event) => {
    this.setState({
      orientationEvent:event,
      canPlay:true,
    });
  } 

  render(){
    return (
      <>
        <Navbar bg="light" expand="lg">
          <Navbar.Brand>Tremor check</Navbar.Brand>
        </Navbar>
        <Card>
          <Card.Body>
          <OrientationTest canPlay={this.state.canPlay} handleOrientation={this.handleOrientation.bind(this)} />
            <Accordion>
                 <Card>
                <Card.Header>
                  <Accordion.Toggle as={Button} variant="link" eventKey="1">
                    Device information
                  </Accordion.Toggle>
                </Card.Header>
                <Accordion.Collapse eventKey="1">
                  <Card.Body>
                    <p>
                      The below is showing your devices orientation, which is used to play this game. If the numbers are not changing as you move the device, the game will not work for you.
                    </p>
                    <OrientationDisplay event={this.state.orientationEvent} />
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
          </Accordion>
          </Card.Body>
        </Card>
      </>
    )
  }
}
export default Tremor;